<template>
  <div class="lp_page">
    <div class="lp_switch">
      <el-switch
        v-model="onlyIn"
        inactive-color="#BECBD2"
        active-color="#56C9C2"
      ></el-switch>
      只看已投
    </div>

    <div class="hot_lp_item" v-for="(value, index) in tokens" :key="index" v-show="value.invested || !onlyIn">
      <div class="new"></div>
      <el-row type="flex" justify="space-between">
        <el-col :span="18" class="title">{{ value.token }}</el-col>
        <el-col :span="6" class="col_right">
          <div class="icon"></div>
        </el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">总质押：</el-col>
        <el-col :span="12" class="col_right">
          {{ value.total }} {{ value.token }}
        </el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">日产量：</el-col>
        <el-col :span="12" class="col_right">{{ value.output }}</el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">当前用户总收益：</el-col>
        <el-col :span="12" class="col_right">{{ value.totalIncome }}%</el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">我的质押：</el-col>
        <el-col :span="12" class="col_right">{{ value.myPledge }} {{ value.token }}</el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">我的收益：</el-col>
        <el-col :span="12" class="col_right">{{ value.myIncome }} HOT</el-col>
      </el-row>

       <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">可提取</el-col>
        <el-col :span="12" class="col_right">{{ value.canWithdraw }} HOT</el-col>
      </el-row>

      <el-row type="flex" :gutter="20" justify="space-between">
        <el-col>
          <div class="button button_border" @click="onWithDraw(index,value.myPledge)">撤回</div>
        </el-col>
        <el-col>
          <!-- <div class="button button_border" @click="deposit(index)">质押</div> -->
          <div class="button button_border"  @click="showPop(index,value.token)">质押</div>
        </el-col>
      </el-row>

      <div class="button button_fill" @click="approve(value.token,value.address)" v-if="value.isAllow">授权HOT-HBTC</div>
      <div  class="button button_fill" @click="onWithDraw(index,1)">领取收益</div>

      <el-row type="flex" justify="space-between">
        <el-col :span="16" class="can_use_left"
          >可用：{{ value.balance }}</el-col
        >
      </el-row>
    </div>
    <!--列表结束-->

   <div class="shadow" v-show="vpop">
       

      <div class="my_form">
        <div class="title">质押 {{currentCoin}} <button @click="handleClose">关闭</button></div>
        <div class="input_box">
          <input type="number"  v-model="amount" />
          <span>{{currentCoin}}</span>
          <span class="btn_all"  @click="all">全部</span><!--全部-->
        </div>

        <div class="balance">
         余额：
          <span class="red">{{popBalance}} {{currentCoin}}</span>
        </div><!--质押-->

      

		<div class="sub_btn">
			
			<button class="common_btn" @click="deposit(index)">质押</button>
		</div>

      </div>
   </div>

  </div>
</template>

<script>
import Net from "@/assets/net.js";
 var mintContract = new web3.eth.Contract(Net.Mintabi, Net.minContact);
import  { BigNumber }  from 'bignumber.js'
export default {
  data() {
    return {
      drawer: false,
      direction: 'btt',
      amount:"",
      currentCoin:"",
      popBalance:"--",
      index:0,
      tokens: [
        {
          pid: 0,
          token: "HT",
          total: "--",
          output:"--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:true,
          address:Net.HT,
          isAllow:false,
          canWithdraw:"--",
        },
        {
          pid: 1,
          token: "HUSD",
          total: "--",
          output: "--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:true,
          address:Net.HUSD,
           isAllow:false,
           canWithdraw:"--",
        },
        {
          pid: 2,
          token: "HBTC",
          total:"--",
          output:"--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:false,
          address:Net.HBTC,
          isAllow:false,
          canWithdraw:"--",
        },
        {
          pid: 3,
          token: "BAGS",
          total: "--",
          output: "--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:false,
          address:Net.BAGS,
           isAllow:false,
           canWithdraw:"--",
        },
      ],
      onlyIn: false,
      showPool:true,
      address:"",
      vpop:false,
    };
  },
   mounted() {
    var _this =this;
    _this.$getAddress();  
    setTimeout(function () {

     _this.isAllowance("HT",0);
     _this.isAllowance("HUSD",1);
     _this.isAllowance("HBTC",2);
     _this.isAllowance("BAGS",3);
     _this.getUserInfo(0);
     _this.getUserInfo(1);
     _this.getUserInfo(2);
     _this.getUserInfo(3);
     _this.getBalance("HT",0);
     _this.getBalance("HUSD",1)
     _this.getBalance("HBTC",2)
     _this.getBalance("BAGS",3);
     _this.totalPledge("HT",0);
     _this.totalPledge("HUSD",1);
     _this.totalPledge("HBTC",2);
     _this.totalPledge("BAGS",3);

     _this.lookCanWithdraw(0);
     _this.lookCanWithdraw(1);
     _this.lookCanWithdraw(2);
     _this.lookCanWithdraw(3);

  
    //  _this.numByPerDay("HT",0);
    //  _this.numByPerDay("HUSD",1)
    //  _this.numByPerDay("HBTC",2)
    //  _this.numByPerDay("BAGS",3);
     

    }, 1000)
    

  },
  methods:{
    onWithDraw(index,amount){
       //提币或撤回
      var _this = this;
      var value=new BigNumber(amount).multipliedBy(BigNumber(10).pow(18));
      console.log("index:"+index,"value:"+value)
      mintContract.methods
        .withdraw(index,value)
        .send({ from: _this.address })
        .then((res)=>{
           _this.getUserInfo(index)
        })
    },
    showDraw(){
      this.drawer = true
    },
    all(){
      this.amount = this.popBalance
    },
    handleClose () {
      this.vpop = false;
      this.index = "";
      this.currentCoin = "";
      this.popBalance =""
    },
    showPop(index,token){
      var _this = this;
      this.vpop = true;
      this.index = index;
      this.currentCoin = token;
      this.popBalance =_this.tokens[index].balance

      console.log("index:"+this.index+"balance:"+this.popBalance)
    },
    deposit(index) {
      //质押
      var _this = this;
      var value=new BigNumber(_this.amount).multipliedBy(BigNumber(10).pow(18));
      console.log("index:"+index,"value:"+value)
      mintContract.methods
        .deposit(index,value)
        .send({ from: _this.address })
        .then((res)=>{
           _this.getUserInfo(index)
           _this.vpop = false;
        })
    },
    approve(token,address) { //授权
      console.log(token,address,Net.ERC20abi)
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.ERC20abi, address);
      var x=new BigNumber(10000000)
      var value=x.multipliedBy(BigNumber(10).pow(18));

      console.log(value)

     // console.log("查看bignumber"+_this.BigNumber)

      //return 

      currentToken.methods
        .approve(Net.minContact, value)
        .send({from:_this.address})
        .then(function (res) {
          
        });
    },
    isAllowance(token,index) { //查询是否授权
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.ERC20abi, Net[token]);
      console.log(_this.address,Net.minContact)
      currentToken.methods
        .allowance(_this.address,Net.minContact)
        .call({from:_this.address})
        .then(function (res) { 
            res > 0?_this.tokens[index].isAllow = false : _this.tokens[index].isAllow = true
            console.log(token+"授权数量："+res /1e18)
        });
    },
    getUserInfo(index){ //我的质押和收益
      var _this = this;
      mintContract.methods
        .userInfo(index,_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].myPledge= res.amount / 1e18
          _this.tokens[index].myIncome =res.rewardDebt  / 1e18
           console.log(index+"池子的信息："+JSON.stringify(res))
        });
    },
    getBalance(token,index){ //获取余额
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.ERC20abi, Net[token]);
      currentToken.methods
        .balanceOf(_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].balance = res / 1e18
           console.log(token+"余额："+res / 1e18)
        });
    },
    totalPledge(token,index){ //获取总质押
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.ERC20abi, Net[token]);
      currentToken.methods
        .balanceOf(Net.minContact)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].total = res / 1e18;
           console.log(token+"总质押："+res / 1e18)
        });
    },
    lookCanWithdraw(index){ //查看可提取
      var _this = this;
     
       mintContract.methods
        .pendingHog(index,_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].canWithdraw = res / 1e18;
           console.log(index+"池可提取："+res / 1e18)
        });
    },

    numByPerDay(token,index){ //日产量
      var _this = this;
       mintContract.methods
        .numByPerDay(index)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].output = res / 1e18;
           console.log(index+"日产量："+res / 1e18)
        });
    },
  }
};
</script>

<style lang="scss" scoped>


</style>